import tabs from "../../components/common/tabs/tabs.vue";
import pushstep from "../../components/pushTask/pushstep/pushstep.vue"
import stepCard from "../../components/stepCard/stepCard.vue"
export default {
    components:{
        tabs,pushstep,stepCard
    },
    created(){
        // 请求服务费接口
        this.getServePrice();
      // 为图片上传添加请求头
        uni.getStorage({
            key:'token',
            success:(res)=>{
                this.headerData.token=res.data
            }
        })
    },
    data() {
        return {
            URL:getApp().globalData.URL,
            // 界面任务内容 
            taskType:0,    
            // 是否开启添加任务步骤窗口
            pushTaskBox:false,
            // 服务费用
            dytaskServePrice:0,
            taskServePrice:0,
            rewardTaskServePrice:0,
            taskData:{
              title: "",
            price: "",
            quota: "",
            content: "",  
            },
            //抖音页  是否选择了图片
            isImg: false,
            // 上传路径
            action: this.URL+'/api/v1/push_dy_task',
            formData: {
                title: '',
                content: '',
                price: '',
                quota: '',
                dy_url: '',
            },
            headerData: {
                token: null
            },
            // 悬赏任务步骤数组
            currentPushTaskBoxData:{
                text:'',
                pic:'',
                //悬赏任务页  是否选择了图片
                isImg: false,
            },
            taskStepList:[
                
            ],
            rewardStepList:[],
            rewardGoodsPlatformTypeList: [
            				{
            					name: '淘宝',
            					checked: false,
            					disabled: false
            				},
            				{
            					name: '拼多多',
            					checked: false,
            					disabled: false
            				},
            				{
            					name: '京东',
            					checked: false,
            					disabled: false
            				},
                            
                            {
                            	name: '天猫',
                            	checked: false,
                            	disabled: false
                            },
                            {
                            	name: '阿里',
                            	checked: false,
                            	disabled: false
                            },
                            {
                            	name: '其他',
                            	checked: false,
                            	disabled: false
                            }
            			],
            			
                        rewardGoodsTypeList: [
                        				{
                        					name: '红包试用',
                        					checked: false,
                        					disabled: false
                        				},
                        				{
                        					name: '商品试用',
                        					checked: false,
                        					disabled: false
                        				}
                        			],
                                    
                // 福利任务model
                  rewardTaskObj:{
                      currentRewardGoodsPlatformType: '淘宝',
                      currentRewardGoodsType: '红包试用',
                      title:"",
                      goodsUrl:"",
                      liaison:"",
                      price:"",
                      moneyReward:0,
                      quota:"",
                      content:"",
                      keyword:"",
                      isBtn:false,
                      isBtnStatus:0,
                      
                  }   
        }
    },
    methods: {
        async getServePrice(){
           await this.$u.get('/get_serve_price',{name:'push_task'}).then(res=>this.taskServePrice=res.data)
           await this.$u.get('/get_serve_price',{name:'push_dy_task'}).then(res=>this.dytaskServePrice=res.data),
           await this.$u.get('/get_serve_price',{name:'push_reward_task'}).then(res=>this.rewardTaskServePrice=res.data)
        },
        // 网络请求返回过滤
        response(res){
          if (res.errorCode === 10000) {
              uni.showToast({
                  title: res.msg,
                  icon: "none"
              })
              return false;
          } else if (res.errorCode === 20003) {
              uni.showToast({
                  title: res.msg,
                  icon: "none"
              })
              setTimeout(() => {
                  uni.reLaunch({
                      url: "../login/login"
                  })
                  return false;
              }, 1500)
          } else {
              return true;
          }
        },
        // post
        post(){
            if(this.taskType===0){
                this.postDyTask();
            }
            if(this.taskType===1){
                this.postTask();
            }
            if(this.taskType===2){
              this.postRewardTask();
            }
        },
        // tabs 监听
        changeTabIndex(index){
            // 0 抖音  1 普通任务
            this.taskType=index;
        },
        hidePushTaskBox(){
           this.pushTaskBox=false;
        },
        openPushTaskBox(){
           this.pushTaskBox=true;
        },
        imglist(res) {

        },
        dycheckParam() {
            if (this.$u.test.empty(this.formData.title)) {
                uni.showToast({
                    title: "你还没有填写标题",
                    icon: "none"
                })
                return false;
            };
            if (this.$u.test.empty(this.formData.price)) {
                uni.showToast({
                    title: "你还没有填写点赞价格",
                    icon: "none"
                })
                return false;
            };
            if (this.$u.test.empty(this.formData.dy_url)) {
                uni.showToast({
                    title: "你还没有填写视频链接",
                    icon: "none"
                })
                return false;
            };
            if (this.$u.test.empty(this.formData.quota)) {
                uni.showToast({
                    title: "你还没有填写发布数量",
                    icon: "none"
                })
                return false;
            };
            if (this.$u.test.empty(this.formData.content)) {
                uni.showToast({
                    title: "你还没有填写任务说明",
                    icon: "none"
                })
                return false;
            };

                return true;
        },
        postDyTask() {
            if (!this.dycheckParam()) return;
            // 上传
            this.$refs.uUpload.upload();
        },
        taskcheckParam() {
            if (this.$u.test.empty(this.taskData.title)) {
                uni.showToast({
                    title: "你还没有填写标题",
                    icon: "none"
                })
                return false;
            };
            if (this.taskData.title.length > 10 ) {
                uni.showToast({
                    title: "标题太长了",
                    icon: "none"
                })
                return false;
            };
            if (this.$u.test.empty(this.taskData.price)) {
                uni.showToast({
                    title: "你还没有填写任务价格",
                    icon: "none"
                })
                return false;
            };
            if (this.$u.test.empty(this.taskData.quota)) {
                uni.showToast({
                    title: "你还没有填写发布数量",
                    icon: "none"
                })
                return false;
            };
            if (this.$u.test.empty(this.taskData.content)) {
                uni.showToast({
                    title: "你还没有填写任务说明",
                    icon: "none"
                })
                return false;
            };
            // 验证是否有步骤图片
            if (this.$u.test.empty(this.taskStepList)) {
                uni.showToast({
                    title: "至少添加一个步骤",
                    icon: "none"
                })
                return false;
            }
                return true;
        },
        async postTask(){
            if(!this.taskcheckParam())return;
           await this.$u.post('/push_task',{
                  title:this.taskData.title,
                  price:this.taskData.price,
                  quota:this.taskData.quota,
                  content:this.taskData.content,
                task_step_list:JSON.stringify(this.taskStepList)
            }).then(res=>{
                uni.showToast({
                    title: res.msg,
                }) 
                setTimeout(()=>{
                   uni.switchTab({
                       url:"../home/home"
                   })
                },2000)
                
            })
        },
        rewardtaskcheckParam() {
            if (this.$u.test.empty(this.rewardTaskObj.currentRewardGoodsPlatformType)) {
                uni.showToast({
                    title: "试用平台参数错误",
                    icon: "none"
                })
                return false;
            };
            if (this.$u.test.empty(this.rewardTaskObj.currentRewardGoodsType)) {
                uni.showToast({
                    title: "试用type参数错误",
                    icon: "none"
                })
                return false;
            };
            if (this.rewardTaskObj.title.length > 15  ) {
                uni.showToast({
                    title: "标题控制需在15字内",
                    icon: "none"
                })
                return false;
            };
            
            if (this.$u.test.empty(this.rewardTaskObj.title)) {
                uni.showToast({
                    title: "标题不能为空",
                    icon: "none"
                })
                return false;
            };
            if (this.$u.test.empty(this.rewardTaskObj.price)) {
                uni.showToast({
                    title: "你还没有填写任务价格",
                    icon: "none"
                })
                return false;
            };
            if (this.$u.test.empty(this.rewardTaskObj.moneyReward)) {
                uni.showToast({
                    title: "你还没有填写红包金额",
                    icon: "none"
                })
                return false;
            };
            if (this.$u.test.empty(this.rewardTaskObj.quota)) {
                uni.showToast({
                    title: "你还没有填写发布数量",
                    icon: "none"
                })
                return false;
            };
            if (this.$u.test.empty(this.rewardTaskObj.content)) {
                uni.showToast({
                    title: "你还没有填写要求",
                    icon: "none"
                })
                return false;
            };
            if (this.$u.test.empty(this.rewardTaskObj.keyword)) {
                uni.showToast({
                    title: "你还没有填写商品关键词",
                    icon: "none"
                })
                return false;
            };
            // 验证是否有步骤图片
            if (this.$u.test.empty(this.rewardStepList)) {
                uni.showToast({
                    title: "至少添加一个步骤",
                    icon: "none"
                })
                return false;
            }
            //修改下内容
            if(this.rewardTaskObj.isBtn){
                this.rewardTaskObj.isBtnStatus=1;
            }else{
                this.rewardTaskObj.isBtnStatus=0;
            }
                return true;
        },
        async postRewardTask(){
            if(!this.rewardtaskcheckParam())return;
            await this.$u.post('/push_reward_task',{
                reward_goods_platform_type:this.rewardTaskObj.currentRewardGoodsPlatformType,
                reward_goods_type:this.rewardTaskObj.currentRewardGoodsType,
                   title:this.rewardTaskObj.title,
                   goods_url:this.rewardTaskObj.goodsUrl,
                   liaison:this.rewardTaskObj.liaison,
                   price:this.rewardTaskObj.price,
                   money_reward:this.rewardTaskObj.moneyReward,
                   quota:this.rewardTaskObj.quota,
                   content:this.rewardTaskObj.content,
                   keyword:this.rewardTaskObj.keyword,
                   is_btn:this.rewardTaskObj.isBtnStatus,
                 reward_task_step_list:JSON.stringify(this.rewardStepList)
             }).then(res=>{
                 uni.showToast({
                     title: res.msg,
                 }) 
                 setTimeout(()=>{
                    uni.switchTab({
                        url:"../home/home"
                    })
                 },2000)
                 
             })
        },
        oversize() {
            uni.showToast({
                title: "图片过大，请重新上传",
                icon: 'none'
            })
        },
        //抖音图片上传成功 任务发布完成
        success(data) {
            let data2 = JSON.parse(data)
             if(!this.response(data2)) return;
            if (data2.errorCode === 0) {
                uni.showToast({
                    title: data2.msg
                })
                setTimeout(() => {
                    uni.switchTab({
                        url: '../home/home'
                    })
                }, 1500)
                return;
            }
            if (data2.errorCode === 20010) {
                uni.showToast({
                    title: data2.msg,
                    icon: 'none'
                })
                setTimeout(() => {
                    uni.switchTab({
                        url: '../home/home'
                    })
                }, 1500)
                return;
            }
            if (data2.errorCode === 20005) {
                uni.showToast({
                    title: data2.msg,
                    icon: 'none'
                })
                setTimeout(() => {
                    uni.switchTab({
                        url: '../home/home'
                    })
                }, 1500)
                return;
            }
        },
        // 悬赏任务步骤图上传成功
        taskstepPicSuccess(data){
            let data2 = JSON.parse(data)
            if (data2.errorCode === 0) {
                this.currentPushTaskBoxData.pic=data2.data;
            }
        },
        //图片上传失败
        error(res) {
            uni.showToast({
                title: "上传遇到未知错误",
                icon: 'none'
            })

        },
        //抖音页
        complete(list) {
            this.isImg = true
        },
        remove(index) {
            this.isImg = false
        },
        // 悬赏任务页
        taskComplete(list) {
            this.currentPushTaskBoxData.isImg = true
        },
        taskRemove(index) { 
            this.currentPushTaskBoxData.isImg = false
        },
        
        // 粘贴url
        pasteUrl(){
            uni.getClipboardData({
                success:  (res) =>{
                   this.rewardTaskObj.goodsUrl=res;
                }
            });
        },
        openAddStepComponent(type){
            switch(type){
                case 'reward':
                 this.$refs.rewardStepComponent.showBox();
                 return;
                 case 'task':
                 this.$refs.taskStepComponent.showBox();
                 return;
            }
         
            
        },
        updateRewardStepList(list){
            this.rewardStepList=list;
        },
        updateTaskStepList(list){
            this.taskStepList=list;
        },

        
    },
    computed: {
        payment() {

            if(this.taskType===0){
               return (this.formData.price * this.formData.quota) + (this.dytaskServePrice * this.formData.quota); 
            }
            if(this.taskType===1){
                return (this.taskData.price * this.taskData.quota) + (this.taskServePrice * this.taskData.quota); 
            }
            if(this.taskType===2){
                return (this.rewardTaskObj.price* this.rewardTaskObj.quota) + (this.rewardTaskServePrice * this.rewardTaskObj.quota)+(this.rewardTaskObj.moneyReward* this.rewardTaskObj.quota); 
            }

            
        }
    }

}
